.menu{
	width:15rem;
	background:#333;
	height:90%;
	padding:1rem ;
	font-size:1.5rem;
	font-weight:bold;
	color:white;
	position:fixed;
	display:flex;
	flex-direction:column;
	justify-content:space-between;
	overflow-y:auto;
	left:0;
	top:5rem;
	transition:transform 0.5s ease-in-out;
}

.menu-heading{
	border-bottom:1px solid #ccc;
}

.menu ul li{
	cursor:pointer;
	list-style-type:none;
	display:flex;
	align-items:center;
	margin-bottom:15px;
	font-size:1.25rem;
	padding:0 5px;
}

.icon{
	margin-right:15px;
}

.menu ul{
	padding:0;
}

.menu li:hover{
	transform:scale(1.05);
}

.menu-footer{
	font-size:1rem;
	
}

@media(max-width:768px){
	.menu{
		width:10rem;
		height:90%;
		position:fixed;
		z-index:1;
	}

	.menu-heading,.menu ul li{
		font-size:1rem;
	}
	
	.icon{
		display:none;
	}
}

