.header{
	display:flex;
	align-items:center;
	padding:0 25px;
	background:#444;
	position:fixed;
	top:0;
	height:5rem;
	width:100%;
	z-index:1;
}

.logo{
	width:50px;
	margin-left:25px;
}

.text{
	font-size:2.5rem;
	margin:0 auto 0 15px;
}

.menu-box{
	cursor:pointer;
	position:relative;
	width:40px;
	height:25px;
}

.menu-bar{
	width:25px;
	border-bottom:2px solid white;
	position:absolute;
	transition:transform 0.25s ease-in-out;
	left:7.5px;
}

.search-bar{
	width:40rem;
	border-radius:5px;
	background:#ccc;
	display:flex;
	align-items:center;
	padding:0 0 0 25px;
}

.search-icon{
	color:#444;
	cursor:pointer;
}

input{
	background:transparent;
	border:none;
	outline:none;
	border-top-right-radius:5px;
	border-bottom-right-radius:5px;
	padding:0 0 0 5px;
	margin-left:15px;
	font-size:1rem;
	width:100%;
	height:35px;
	color:black;
}

@media(max-width:768px){
	.header{
		padding:0 5px;
	}
	
	.text{
		font-size:1.35rem;
	}
	
	.logo{
		width:30px;
		margin-left:10px;
	}
	
	.menu-bar{
		width:12.5px;
	}
	
	.search-bar{
		width:15rem;
		padding:0 0 0 5px;
	}
	
	.input{
		padding:0;
	}
}

