.news-container{
	width:75%;
	margin: auto;
	padding:1rem 0;
	z-index:0;
}

.right{
	transform:translateX(7.5rem);
	transition:transform 0.5s ease-in-out;
}

.left{
	transform:translateX(0);
	transition:transform 0.5s ease-in-out;
}

.errors{
	margin:25% auto;
	background:#911;
	width:50%;
	text-align:center;
	font-size:1.25rem;
	border-radius:7.5px;
	font-weight:bold;
	padding:5px;
	filter:drop-shadow(5px 5px 5px black);
}

.category{
	font-size:2rem;
	font-weight:bold;
	text-transform:capitalize;
	background:#444;
	padding:5px 25px;
}

.containers{
	margin:25px 0;
	border-bottom:1px solid #ccc;
	width:100%;
	padding:25px;
	word-wrap:break-word;
	font-family:Arial;
}

.containers:last-child{
	border:none;
}

.title-img{
	width:100%;
	display:flex;
	justify-content:space-between;
	align-items:center;
}

.title{
	display:block;
	width:100%;
	color:#ddd;
	font-weight:bold;
	text-decoration:none;
	font-size:1.15rem;
}

.description{
	font-style:italic;
	margin-right:5px;
}

.url{
	color:#9cc;
}

.url:hover{
	color:#2ac;
}

.container-text{
	width:65%;
}

.source{
	background:#25f;
	font-weight:bold;
	padding:5px;
	border-radius:10px;
}

.img{
	border-radius:10px;
	width:25%;
	align-self:flex-start;
	height:auto;
}

.news-alt-img{
	background:#444;
	width:25%;
	height:145px;
	border-radius:10px;
	font-size:1.5rem;
	font-weight:bold;
	display:flex;
	justify-content:center;
	align-items:center;	
}

@media(max-width:768px){
	.news-container{
		width:95%;
		z-index:0;
	}
	
	.right, .left{
		transform:translateX(0);
	}
	
	.category{
		font-size:1.125rem;
	}
	
	.title-img{
		position:relative;
		flex-direction:column-reverse;
	}
	
	.container-text{
		width:100%;
		margin-top:15px;
	}
	
	.title{
		font-size:1rem;
	}
	
	.source{
		position:absolute;
		top:0;
		left:0;
	}
	
	.description{
		display:none;
	}
	
	.img{
		width:100%;
	}
}
